* {
	margin: 0;
	padding: 0;
}

/* ListContainer Component CSS */
.list-container-wrapper {
	background-color: rgb(192, 195, 199);
	text-align: center;
	width: 40rem;
	height: auto;
	border-radius: 5px;
	box-shadow: 0px 1px 3px black;
}

.task-input {
	font-size: 1.25rem;
	text-align: center;
	height: 3rem;
	width: inherit;
	outline: none;
	border: none;
	border-radius: 5px;
	border-bottom: 2px solid grey;
}

.empty-tasks {
	padding: 2rem 0 2rem 0;
	font-size: 1.5rem;
}

/* ListItem Component CSS */
.list-item-wrapper {
	background-color: rgb(248, 246, 242);
	text-align: center;
	height: auto;
	width: inherit;
	padding: 0.9rem 0;
	border-bottom: 1px solid black;
	transition: 0.5s;
}

.list-item-wrapper:last-child {
	margin-bottom: 3rem;
}

.check-item {
	float: left;
	margin: 0.3rem 0 0 1rem;
}

.remove-task-btn {
	cursor: default;
}
