* {
	margin: 0;
	padding: 0;
}

.app-wrapper {
	font-family: sans-serif;
	text-align: center;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.app-wrapper hr {
	margin-top: 1rem;
	width: 70%;
	border: none;
	border-bottom: 3px solid rgb(160, 147, 147);
}

.list-title {
	font-family: sans-serif;
	font-weight: lighter;
	font-size: 3rem;
	margin-top: 1rem;
}

.menu-wrapper {
	margin: 1rem 0;
	width: 40rem;
	display: flex;
	justify-content: space-between;
}

.btn-wrapper {
	width: 15rem;
	display: flex;
	justify-content: space-between;
}

.menu-wrapper p {
	padding-top: 3px;
	display: inline-block;
}

.filter-btn {
	padding: 0.25rem;
	border: none;
	border-radius: 3px;
}

.filter-btn:active {
	outline: black;
}
